.profile{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;

  height: 100vh;
  width: 100vw;

  background: yellow;
}

#name{
  grid-column: 2;
  grid-row: 1;
}

#age{
  grid-column: 2;
  grid-row: 2;
}

#desc{
  grid-column: 2;
  grid-row: 3;
}




.projects{
  display: grid;
  grid-template-columns: auto auto auto auto;


  height: 100vh;
  width: 100vw;

  background: pink;

}

#proj{
  background: red;
  border: 5px solid blue;
  height: 200px;
  width: 200px;
}

.test{
  grid-row: span 2;
}


#contact{
  display: grid;
  grid-template-rows: auto;


  height: 100vh;
  width: 100vw;

  background: yellow;
}











/**/


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
